.advent-calendar-banner {
  height: 100%;
  background-color: rgba(0, 0, 0, 0);
  background-image: url("Christmas_7.d33079aa.jpg"), linear-gradient(#9ddad3 0%, #88dad3 29%, #88dad3 100%);
  background-position: 50% 0;
  background-repeat: no-repeat;
  background-size: auto, auto;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  overflow: hidden;
}

@font-face {
  font-family: christmas-second;
  src: url("ChopinScript.4f78160f.ttf");
}

.advent-calendar-banner__big_grid {
  z-index: 3;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  max-width: 1200px;
  grid-template-rows: 1fr;
  grid-template-columns: repeat(2, 1fr);
  margin: auto;
  padding: 0 20px;
  display: grid;
}

.advent-calendar-banner__small_grid {
  z-index: 3;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  color: #000;
  aspect-ratio: 1 / 1;
  background-color: rgba(255, 103, 105, 0);
  border-radius: 5px;
  grid-template-rows: repeat(5, 100px);
  grid-template-columns: repeat(5, 100px);
  align-content: center;
  justify-content: center;
  padding: 0;
  display: grid;
}

.advent-calendar-banner__present {
  text-align: center;
  background-color: #ffe9dc;
  border-radius: 5px;
  list-style: none;
  position: relative;
  box-shadow: 2px 2px 1px 2px #c30004;
}

.advent-calendar-banner__present:hover {
  cursor: pointer;
  transition: scale .25s ease-out;
  scale: 1.03;
}

.advent-calendar-banner__link {
  color: #c30004;
  text-align: center;
  flex-direction: column;
  align-items: center;
  font-family: christmas-second;
  font-size: 1.8em;
  text-decoration: none;
  display: flex;
}

.advent-calendar-banner__day {
  text-align: center;
  font-weight: 700;
  display: inline-block;
}

.advent-calendar-banner__present-image {
  width: 50px;
  height: 50px;
  border-radius: 25%;
  margin: 0 auto;
}

.advent-calendar-banner__present-image:hover {
  cursor: pointer;
  background: rgba(255, 255, 255, .1);
  animation: shake .5s;
  box-shadow: 0 0 5px 10px rgba(255, 255, 255, .1);
}

@media (max-width: 1200px) {
  .advent-calendar-banner__big_grid {
    max-width: 800px;
  }

  .advent-calendar-banner__small_grid {
    z-index: 3;
    aspect-ratio: 4 / 6;
    grid-template-rows: repeat(6, 80px);
    grid-template-columns: repeat(4, 80px);
    display: grid;
  }
}

@media (max-width: 760px) {
  .advent-calendar-banner__big_grid {
    max-width: 1600px;
    grid-template-rows: 1fr;
    grid-template-columns: repeat(1, 1fr);
    padding: 20px;
  }

  .advent-calendar-banner__small_grid {
    aspect-ratio: 6 / 4;
    color: #fff;
    grid-template-rows: repeat(4, 1fr);
    grid-template-columns: repeat(6, 1fr);
    padding: 0;
  }
}

@media (max-width: 580px) {
  .advent-calendar-banner__small_grid {
    aspect-ratio: 4 / 6;
    color: #fff;
    grid-template-rows: repeat(6, 1fr);
    grid-template-columns: repeat(4, 1fr);
    padding: 0;
  }
}

@keyframes shake {
  0% {
    transform: translate(1px, 1px)rotate(0deg);
  }

  10% {
    transform: translate(-1px, -2px)rotate(-1deg);
  }

  20% {
    transform: translate(-3px)rotate(1deg);
  }

  30% {
    transform: translate(3px, 2px)rotate(0deg);
  }

  40% {
    transform: translate(1px, -1px)rotate(1deg);
  }

  50% {
    transform: translate(-1px, -2px)rotate(-1deg);
  }

  60% {
    transform: translate(-3px, 1px)rotate(0deg);
  }

  70% {
    transform: translate(3px, 1px)rotate(-1deg);
  }

  80% {
    transform: translate(-1px, -1px)rotate(1deg);
  }

  90% {
    transform: translate(1px, -2px)rotate(0deg);
  }

  100% {
    transform: translate(1px, -2px)rotate(-1deg);
  }
}

@font-face {
  font-family: christmas;
  src: url("PWHappyChristmas.932a623a.ttf");
}

.advent-calendar-banner__description {
  z-index: 3;
  max-width: 600px;
  flex-direction: column;
  justify-content: end;
  margin: 0;
  display: flex;
}

.advent-calendar-banner__text {
  color: #fff;
  text-wrap: balance;
  text-align: center;
  background: #ff6769;
  background: -webkit-linear-gradient(to top, white, white);
  -webkit-text-fill-color: transparent;
  background: linear-gradient(to top, #fff, #fff);
  -webkit-background-clip: text;
  background-clip: text;
  padding-left: 10px;
  font-family: christmas;
  font-size: 2.5em;
  font-weight: 300;
}

